                </div>
                <div class="column-value">
                    {{ $utils.formatNumber(amount, 'en-US', 0, 6) }} <span >USDT</span>
                </div>
            </div>
        </div>

        <div class="row-3">
            <div class="column-4">
                <button class="return-button" @click="returnReferralDeposit">
                    {{ $localizeFilter('cancel') }}
                </button>
            </div>
        </div>
    </div>
