<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: ['referral'],
    setup() {
        return {}
    },
    methods: {
        returnReferralDeposit() {
            this.$emit(
                'returnReferralDeposit',
                this.referral
            )
        },
    },
    computed: {
        profile() {
            return this.$store.getters.profile
        },
        amount() {
            return (this.referral.amount || 0) / 1000000
        },
    }
})
</script>

