<template>
    <div class="referral-deposit-list">

        <button class="back-button" @click="$router.back()">
            <img class="back-button-arrow-ref" src="@/assets/newImages/sidebar/double-arrows.svg" />
            <p class="back-button-text">{{ $localizeFilter('liquidity_provider') }}</p>
        </button>

        <div class="title-block">
            <div class="title">{{ $localizeFilter('referral_deposit_list_title') }}</div>
            <!-- <button class="info-button" @click="openInfoModal"></button> -->
        </div>
        <!-- <div class="subtitle"></div> -->

        <div class="search">
            <p class="search-title" style="display: none">{{ $localizeFilter('search') + ':' }}</p>
            <input class="search-field" v-model="search" :placeholder="$localizeFilter('enter_id_telegram_nickname_email')">
            <button class="basic-button search-button" @click="startSearch()"></button>
        </div>

        <!-- <div class="referrals-block">
            <p class="refferals-text">
                {{$localizeFilter('all_referrals_with_credit_deposit')}}
            </p>
        </div> -->
        <EmptyTable v-if="referrals.length === 0" :text="$localizeFilter('no_referrals')" />
        <div class="referrals-wrapper" v-else>
            <div class="rating-table-header">
                <div class="rating-table-header-img"></div>
                <p>{{ $localizeFilter('all_referrals') }}</p>
            </div>
            <div class="referrals-container">
                <div v-for="(referral, idx) in referrals" :key="idx">
                    <ReferralDeposit :referral="referral" @returnReferralDeposit="returnReferralDeposit" />
                </div>

                <div class="load-more-button-container" v-if="referrals.length > 0">
                    <button class="load-more-button" @click="loadMoreReferrals()">
                        {{ $localizeFilter('load_more') }}
                    </button>
                </div>

            </div>
        </div>


        <ReferralCreditDepositReturnModal v-if="showReturnDepositModal" :referral="returnDepositModalReferral"
            @reloadData="reloadData" @closeModal="closeModal" />
    </div>
</template>
<script>
import { defineComponent } from '@vue/composition-api'
import ReferralDeposit from '../components/ReferralCreditDeposit.vue'
import BuySellToggle from '../components/BuySellToggle.vue'
import ReferralCreditDepositReturnModal from '../components/newModals/ReferralCreditDepositReturnModal.vue'
import EmptyTable from '../components/EmptyTable.vue'

export default defineComponent({
    components: {
        ReferralDeposit,
        BuySellToggle,
        ReferralCreditDepositReturnModal,
        EmptyTable
    },
    props: {
    },
    setup() {
    },
    data: () => ({
        showReturnDepositModal: false,
        returnDepositModalReferral: null,
        referrals: [],
        pageSize: 20,
        page: 1,
        search: '',
    }),
    computed: {
        skip() {
            return (this.page - 1) * this.pageSize
        },
        limit() {
            return this.pageSize
        },
    },
    methods: {
        startSearch() {
            this.loadData()
        },
        closeModal(reload) {
            this.showReturnDepositModal = false
            this.returnDepositModalReferral = null
            if (reload) {
                this.reloadData()
            }
        },
        async reloadData() {
            await this.loadData(true)
        },
        async loadData(silentCatch = false, lazyLoading = false) {
            try {
                if (!lazyLoading) {
                    this.page = 1
                }
                else {
                    if (this.referrals.length % this.pageSize === 0) {
                        this.page++
                    }
                }
                const data = {
                    search: this.search,
                    skip: this.skip,
                    limit: this.limit
                }
                const result = await this.$store.dispatch('getReferralsWithSponsorCreditDeposit', data)
                let newReferrals = result
                // this.$store.dispatch('runNextAuctionRoundTimer', {timestamp: result.nextRoundTimestamp})
                for (let i = 0; i < newReferrals.length; i++) {
                    newReferrals[i].type = this.selectedAction
                }
                if (lazyLoading) {
                    if (this.referrals.length % this.pageSize === 0) {
                        this.referrals = this.referrals.concat(newReferrals)
                        if (newReferrals.length === 0) {
                            this.page--
                        }
                    } else {
                        this.referrals = this.referrals.slice(0, this.referrals.length - (this.referrals.length % this.pageSize)).concat(newReferrals)
                    }
                }
                else {
                    this.referrals = newReferrals
                }
            }
            catch (e) {
                if (!silentCatch) {
                    this.$error(e)
                }
            }
        },
        returnReferralDeposit(referral) {
            this.returnDepositModalReferral = referral
            this.showReturnDepositModal = true
        },
        loadMoreReferrals() {
            this.loadData(true, true)
        }
    },
    async mounted() {
        await this.loadData()
    },
})
</script>

